  flex-direction: column;
  height: 100%;
  align-items: center;
}

.head {
  /* border: 2px solid red; */
  width: 100%;
  font-size: 1.25em;
  /* color: blue; */
  
  /* margin-top: 0.93em; */
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  margin-right: 3%;
}

.legend-language{
  width: 85%;
  display: flex;
  /* padding-left: 3%; */
  margin-top: 0.4em;
  /* margin-right: 1.4em; */
  align-items:flex-start;
  justify-content: space-between;
}

.subtitle{
  padding: 5px;
