.grid {
  /* border: 2px solid red; */
  margin: 0 auto;
  width: 95%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 19.375em);
  grid-row-gap: 1.25em;
  row-gap: 1.25em;
  justify-items:center;
  justify-content: space-evenly;
  align-items: stretch;
  align-content: center;
  padding-bottom: 20px;
  
}


.buttonCont {
  /* border: 2px solid red; */
  display: flex;
