  /* margin-top: 1.5625em; */
  justify-content:center;
  
}

.btnPrev {
  cursor: pointer;
  background-image: url(/LibrosGratuitos/static/media/Actions-go-previous-icon.cc850481.png);
  background-repeat: no-repeat;
  width: 7.5em;
  background-size: 2.5em;
  background-position: left;
  text-align: right;
  font-size: 1.125em;
  color: #3d4bca;
  background-color: transparent;
  border: none;
}

.nroPag {
  display:flex;
  justify-content: center;
  width: 4.25em;
  /* text-align: center; */
  font-size: 1.25em;
  background-color: #6c75c7;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 0.5em;
  margin-left: 2%;
  margin-right: 2%;
}

.btnNext {
  cursor: pointer;
  background-image: url(/LibrosGratuitos/static/media/Actions-go-next-icon.cef9a0e0.png);
  background-repeat: no-repeat;
  /* height: 40px; */
  width: 7.5em;
  background-size: 2.5em;
  background-position: right;
  text-align: left;
  font-size: 1.125em;
  color: #3d4bca;
  background-color: transparent;
  border: none;
}

.btnPrev:hover, .btnNext:hover{
  color:tomato;
}

.btnPrev:disabled, .btnNext:disabled{
  background-image: none;
  color: transparent;
  cursor: none;
  cursor:auto;
}


@media (max-width: 450px) {
  .head {
    flex-direction: column;
  }
  .legend-language{
    width: 100%;
  }
}
